/* Global styles */
@import url('~@adobe/aem-core-cif-react-components/dist/main.css');
html,
body {
    border: 0;
    font: inherit;
    font-size: 100%;
    margin: 0;
    padding: 0;
    vertical-align: baseline;
}

body {
    background-color: #ffffff;
}

h1,
h2,
h3,
h4,
h5,
p,
.nav-container,
.rw-box {
    font-family: 'Helvetica Neue', Helvetica, Arial, sans-serif !important;
    color: #000000;
}
